
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import { Line } from "vue-chartjs";

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

import { defineComponent } from "vue";

export default defineComponent({
  components: {
    Line,
  },
  props: {
    data: { type: Array, required: true },
    dataLabel: { type: String, required: true },
  },
  data() {
    return {
      options: {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          y: {
            min: 0,
            max: 100,
            ticks: {
              stepSize: 10,
            },
          },
        },
      },
    };
  },
  computed: {
    datasets() {
      const dataset: object = {
        label: this.dataLabel,
        backgroundColor: "#f87979",
        data: [...this.data],
      };

      return [dataset];
    },

    labels() {
      const array = [];
      for (let i = 6; i <= 23; i++) {
        array.push(i.toString());
      }
      return array;
    },
  },
});
