import axios from "axios";

const API_BASE = "http://diaryapp.dnsuser.de:5050/";

export abstract class ApiHelper {
  public static async get(
    url: string,
    params: any | never
  ): Promise<any | null> {
    if (url.startsWith("/")) {
      url = url.substring(1);
    }

    const requestUrl = `${API_BASE}${url}`;
    const { data } = await axios.get(requestUrl, { params });
    return data;
  }

  public static async post(url: string, data: any): Promise<void> {
    if (url.startsWith("/")) {
      url = url.substring(1);
    }

    const requestUrl = `${API_BASE}${url}`;

    return await axios.post(requestUrl, data);
  }
}
