
import { Options, Vue } from "vue-class-component";
import LineChart from "@/components/LineChart.vue";
import { ApiHelper } from "@/helper/ApiHelper";
import { DiaryEntry } from "@/classes/DiaryEntry";
import { Mood } from "@/classes/Mood";
import { debounce } from "lodash";

@Options({
  components: { LineChart },
})
export default class DetailView extends Vue {
  moodData: Array<number | null> = [];
  moods: Mood[][] = [];
  diaryEntry: DiaryEntry | null = null;

  inputRule(value: number): boolean | string {
    if (!value) return true;
    else if (value > 0 && value <= 100) return true;
    else return "Der Wert muss zwischen 0 und 100 liegen";
  }

  async fetchData() {
    const diaryEntry = await ApiHelper.get("diary", {
      date: new Date().toISOString(),
    });

    this.diaryEntry = diaryEntry;
    const moods: Array<Mood> = diaryEntry?.moods ?? [];

    for (let i = 6; i <= 23; i++) {
      if (!moods.find((mood) => mood.time === i)) {
        moods.push({ time: i, moodCount: null });
      }
    }
    moods.sort((a, b) => {
      return a.time - b.time;
    });

    this.moods = [moods.slice(0, 6), moods.slice(6, 12), moods.slice(12)];
    this.moodData = moods.map((mood) => mood.moodCount);
  }

  async beforeMount() {
    await this.fetchData();
  }

  updateMood = debounce(async (moodCount: number | null) => {
    if (!moodCount || (moodCount && (moodCount <= 0 || moodCount >= 100))) {
      return;
    }

    const moods = [...this.moods[0], ...this.moods[1], ...this.moods[2]];
    const diaryEntry =
      this.diaryEntry || new DiaryEntry(moods, new Date().toISOString());

    await ApiHelper.post("diary", diaryEntry);
    await this.fetchData();
  }, 500);
}
